import React from 'react';
import RideData from './ridedata';
import { array, number, func } from 'prop-types';
import historyIcon from '../images/History.png';


const RideDataList = props => {

    return (
        <div>
            <div id='datalist-title-content'>
                <img id='datalist-img' src={historyIcon} alt='History Icon' />
                <h1 id='datalist-title'>Past Rides</h1>
            </div>
            <div id='datalist-content'>

                {props.pastRideData.length > 0 ? (
                    props.pastRideData.map(ride =>(
                <div className='container datalist-containe' key = {ride.id}>
                    <div className='row'>
                        <div className='col-md-4 datalist-content'>{`${ride.firstName} ${ride.lastName}`}</div>
                        <div className='col-md-4 datalist-content'>{ride.pickUp}</div>
                        {/* attach the event handler passed in as a prop */}
                        <div className='col-md-4 datalist-content'><button onClick={() =>props.deleteRides(ride.id)}
                            className='btn btn-danger btn-sm delete-button pull-right'> x </button>
                        </div>
                    </div>
                    <div className='row'>
                        <div className='col-md-4 datalist-content'>{ride.date}</div>
                        <div className='col-md-4 datalist-content'>{ride.dropOff}</div>
                        {/* attach the event handler passed in as a prop */}
                        <div className='col-md-4 datalist-content'><button onClick={() => props.onRideEditButton(ride.id, ride)}
                            className='btn btn-info btn-sm update-button pull-right'>&#x21bb;</button>
                        </div>
                    </div>
                </div>
                ))
                ) : (
                  <div>
                    <div>Take a Trip</div>
                  </div>
                )}

            </div>

            {/* Pass monthlyRides and averageRideLength as props
            to RideData */}

            <RideData
            monthlyRides={props.monthlyrides}
            averageRideLength={props.averageRideLength} 
            />
        </div>
    );


};



export default RideDataList;