import React , {useState} from 'react';
import carIcon from '../images/car-icon.png';

const BookRideForm = ({addRides}) => {
const initialFormState = {id:null, firstName: '', lastName: '', pickUp: '', dropOff: '', date: new Date().toLocaleDateString()}
const [ride, setRide] = useState(initialFormState)

const handleInputChange = event => {
    const { name, value} = event.target

    setRide({...ride, [name]: value })
}
// }
    // 1. Create and initialize state variable for form data.
    // 2. Implement two-way binding on form inputs.
    // 3. Implement an onSubmit function for the form to call
    //    addRide function passed in as a prop. Then clear
    //    the form.

console.log(ride)
    return (
        <div>
            <div id='form-title-content'>
                <img id='form-img' src={carIcon} alt='Car Icon' />
                <h1 id='form-title'>Book Ride</h1>
            </div>
            <div id='form-content'>
                <form onSubmit={event => {
                    event.preventDefault()
                    addRides(ride)
                    setRide(initialFormState)
                }
                } id='form-inner-content'>
                    <div className='form-group'>
                        <label htmlFor='first-name'>First Name</label>
                        <input name = 'firstName' placeholder="Enter your First Name" type='text' className='form-control input-form' id='first-name' required value={ride.firstName} onChange = {handleInputChange} />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='last-name'>Last Name</label>
                        <input name = 'lastName' placeholder="Enter your Last Name" type='text' className='form-control input-form' id='last-name' required value={ride.lastName} onChange = {handleInputChange} />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='pick-up'>Pick Up</label>
                        <input name ='pickUp' placeholder="Enter your Pickup Location" type='text' className='form-control input-form' id='pick-up' required value={ride.pickUp} onChange = {handleInputChange} />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='drop-off'>Drop Off</label>
                        <input name = 'dropOff' placeholder="Enter your Drop Off Location" type='text' className='form-control input-form' id='drop-off' required value={ride.dropOff} onChange = {handleInputChange}/>
                    </div>
                    <button type='submit' className='btn btn-primary submit-button'>Submit</button>
                </form>
            </div>
        </div>
    );
}

export default BookRideForm;